import axios from "axios";
import cookie from "cookie_js";

function get_token() {
  return cookie.get("token");
}

export const istance = axios.create({
  baseURL: `https://api.netex.kg/${localStorage.getItem("i18nextLng")}`,
  //baseURL: `https://api.netexkg.webisgroup.ru/${localStorage.getItem("i18nextLng")}`,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json; charset=UTF-8",
    Authorization: `Bearer ${get_token()}`,
  },
});

export const istanceSecond = axios.create({
  baseURL: `https://ns1.netex.kg/`,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json; charset=UTF-8",
    Authorization: `Bearer ${get_token()}`,
  },
});

export let istancePush;

function pushToken(sessionToken) {
  return (istancePush = axios.create({
    baseURL: `https://api.netex.kg/push/`,
    //baseURL: `https://api.netexkg.webisgroup.ru/push/`,
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json; charset=UTF-8",
      Authorization: `Bearer ${sessionToken}`,
    },
  }));
}

istancePush = pushToken(get_token());

// push
export const Pushmessages = {
  saveCodeAndroid(pushkey_android) {
    return istancePush
      .get(`/?pushkey_android=${pushkey_android}`)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
  saveCodeIOS(pushkey_ios) {
    return istancePush
      .get(`/?pushkey_ios=${pushkey_ios}`)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
};
// end push

// auth
export const Register = {
  postRegister(register) {
    return istance
      .post("/auth/registration", register)
      .then((response) => {
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
};

export const Login = {
  postLogin(login) {
    return istance
      .post("/auth/login", login)
      .then((response) => {
        const pushAndroidTokenFromCookie = cookie.get("pushkey_android");
        const isMobileFromCookie = cookie.get("isMobile");
        if (isMobileFromCookie && pushAndroidTokenFromCookie !== undefined) {
          pushToken(response.data.token);
          Pushmessages.saveCodeAndroid(pushAndroidTokenFromCookie);
        }
        return response;
      })
      .catch((error) => {
        return error.response;
      });
  },
};

