// p2p
export const P2P = {
  createDeals(data) {
    return istanceSecond
      .post(`create/`, data)
      .then((res) => res)
      .catch((e) => e.response);
  },
  deleteDeal(id) {
    return istanceSecond
      .delete(`retrieve_update_delete/${id}/`)
      .then((res) => res)
      .catch((e) => e.response);
  },
  listDeals() {
    return istanceSecond
      .get(`/`)
      .then((res) => res.data)
      .catch((e) => e.response);
  },
  dealById(id) {
    return istanceSecond
      .get(`/retrieve_update_delete/${id}/`)
      .then((res) => res.data)
      .catch((e) => e.response);
  },
  joinToDeal(id, data) {
    return istanceSecond
      .put(`/join/${id}/`, data)
      .then((res) => res.data)
      .catch((e) => e.response);
  },

  acceptPaymentSent(id, data, type) {
    return istanceSecond
      .put(
        `/accept_payment${type === "card2" ? "_card2" : ""}/sent/${id}/`,
        data
      )
      .then((res) => res)
      .catch((e) => e.response);
  },
  acceptPaymentGet(id, data, type) {
    return istanceSecond
      .put(
        `/accept_payment${type === "card2" ? "_card2" : ""}/get/${id}/`,
        data
      )
      .then((res) => res)
      .catch((e) => e.response);
  },

  acceptCash(id, acceptType) {
    return istanceSecond
      .put(`/trade/${acceptType === "cash2" ? "accept2" : "accept"}/${id}/`)
      .then((res) => res)
      .catch((e) => e.response);
  },

  myDeals() {
    return istanceSecond
      .get(`/my_trades/`)
      .then((res) => res.data)
      .catch((e) => e.response);
  },
  commission() {
    return istanceSecond
      .get(`/commission/`)
      .then((res) => res.data)
      .catch((e) => e.response);
  },
};

